import * as React from 'react';
import { Box, ThemeProvider, createTheme } from '@mui/system';
import Typography from '@mui/material/Typography';
import { TextField } from '@mui/material';
import { blueGrey, brown, grey } from '@mui/material/colors';
import { HighlightTwoTone } from '@mui/icons-material';
import { useState } from 'react';
import ListBox from './ListBox';
import Button from '@mui/material/Button';


const theme = createTheme({
    palette: {
        background: {
            paper: '#ffffff',
        },
        text: {
            primary: '#173A5E',
            secondary: '#46505A',
        },
        action: {
            active: '#001E3C',
        },
        success: {
            dark: '#009688',
        },

    },
});


function TodoBox(props) {

    const [inputText, setInputText] = useState();


    const handleSubmit = (e) => {
        e.preventDefault();
        let maxId = 0;
        props.taskList.map(task => {
            if (maxId < task.id) {
                maxId = task.id
            };
        });
        //taskを追加する
        props.settaskList([
            ...props.taskList,
            {
                id: maxId + 1,
                text: inputText
            }]);
        setInputText("");

    }

    const handleChange = (e) => {
        setInputText(e.target.value);

    }

    return (
        <>
            <ThemeProvider theme={theme}>

                <Box
                    sx={{
                        bgcolor: 'background.paper',
                        boxShadow: 1,
                        border: 1,
                        borderRadius: 2,
                        p: 2,
                        width: 520,
                        minHeight: 800,
                        margin: 10,
                        marginTop: 0,
                        marginBottom: 0,
                    }}
                >
                    <Box sx={{ color: 'text.primary', fontSize: 40, fontWeight: 'bold', textAlign: 'center' }}>{props.title}</Box>
                    {/* input */}
                    <Box sx={{ backgroundColor: '#fff8e8', color: 'text.primary', fontSize: 28, fontWeight: 'medium', textAlign: 'center', m: 2, borderBottom: 'dashed' }}>
                        <form onSubmit={handleSubmit}>

                            <input type="text" placeholder='Please enter your task' value={inputText} onChange={handleChange} tabindex='1' style={{
                                padding: 15,
                                margin: 1,
                                width: '100%',
                                background: 'none',
                                border: 'none',
                                outline: 'none',
                                fontSize: '90%'
                            }} />
                        </form>
                    </Box>
                    {/* リスト */}

                    <ListBox taskList={props.taskList} settaskList={props.settaskList} nextList={props.nextList} setNextList={props.setNextList} check={props.check} />
                </Box>



            </ThemeProvider>
        </>

    );
}

export default TodoBox;



