import React from 'react';
import { Box, ThemeProvider, createTheme } from '@mui/system';
import Typography from '@mui/material/Typography';
import { TextField } from '@mui/material';
import { blueGrey, brown, grey } from '@mui/material/colors';
import { HighlightTwoTone } from '@mui/icons-material';
import { useState } from 'react';
import ArrowForwardIcon from '@mui/icons-material/ArrowForward';
import ClearIcon from '@mui/icons-material/Clear';
import CheckIcon from '@mui/icons-material/Check';



function ListBox(props) {
    const handleDelete = (id) => {
        props.settaskList(props.taskList.filter((task) => task.id !== id));
        console.log(props.taskList);

    };
    const handleCheck = (id) => {
        let nextTaskText = '';
        props.taskList.map(task => {
            if (task.id === id) {
                nextTaskText = task.text;
            };

        });
        let maxId = 0;
        props.nextList.map(task => {
            if (maxId < task.id) {
                maxId = task.id
            };
        });
        //taskを追加する
        props.setNextList([
            ...props.nextList,
            {
                id: maxId + 1,
                text: nextTaskText
            }]);
        props.settaskList(props.taskList.filter((task) => task.id !== id));


    };
    const checkButton = (id) => {
        if (props.check == 'handleCheck') {
            handleCheck(id);
        };
        if (props.check == 'handleDelete') {
            handleDelete(id)
        };
    };

    const clearButtonStyles = {
        color: 'grey', fontSize: 'medium', textAlign: 'center', verticalAlign: 'center', "&:hover": {
            color: 'text.primary',
            fontWeight: 'bold',
            fontSize: 'large'
        }
    }

    const clearFocusStyles = {
        color: 'text.primary', fontSize: 'large', fontSize: 'large', textAlign: 'center', verticalAlign: 'center', "&:hover": {
            color: 'text.primary',
            fontWeight: 'bold',
            fontSize: 'large'
        }
    }

    const handleFocus = () => {

    };

    return (<>
        {
            props.taskList.map((task, index) => (
                <div>


                    <Box key={index} sx={{ color: 'text.primary', fontSize: 28, fontWeight: 'medium', textAlign: 'center', m: 2, borderBottom: 'dashed' }}>
                        <Box sx={{ display: 'flex', alignItems: 'center' }}>
                            <button type='button' onClick={() => handleDelete(task.id)} style={{
                                background: 'none',
                                // outline: 'none',
                                border: 'none',
                                width: 30,
                                padding: 5,
                            }}>
                                <ClearIcon sx={document.hasFocus() ? clearFocusStyles : clearButtonStyles} />
                            </button>
                            <div style={{
                                flex: 1,
                            }}>
                                <p style={{
                                    margin: 0,
                                    width: 410,
                                    wordWrap: 'break-word'
                                }}>
                                    {task.text}
                                </p>
                            </div>

                            {/* 三項演算子 */}
                            {/* arrowForward or checkicon */}
                            {props.check == 'handleCheck' ?
                                // True
                                <>
                                    <button type='button' onClick={() => checkButton(task.id)} style={{
                                        background: 'none',
                                        // outline: 'none',
                                        border: 'none'
                                    }}>
                                        <ArrowForwardIcon sx={{
                                            color: 'grey', fontSize: 28, fontWeight: 'medium', textAlign: 'right', "&:hover": {
                                                color: 'text.primary',
                                                fontWeight: 'bold',
                                                fontSize: 30,
                                            }
                                        }} />
                                    </button>

                                </>
                                :
                                // False
                                <>
                                    <button type='button' onClick={() => checkButton(task.id)} style={{
                                        background: 'none',
                                        // outline: 'none',
                                        border: 'none'
                                    }}>
                                        <CheckIcon sx={{
                                            color: 'grey', fontSize: 28, fontWeight: 'medium', textAlign: 'right', "&:hover": {
                                                color: '#3fd10f',
                                                fontWeight: 'bold',
                                                fontSize: 30,
                                            }
                                        }} />
                                    </button>

                                </>

                            }


                        </Box>
                    </Box>
                </div>
            ))
        }
    </>
    );
}

export default ListBox;